@include font("lato-regular", lato-regular);
@include font("montserrat-bold", montserrat-bold);
@include font("montserrat-regular", montserrat-regular);
@include font("opensans-ExtraBold", opensans-extrabold);
@include font("opensans-italic", opensans-italic);
@include font("opensans-regular", opensans-regular);
@include font("opensans-semibold", opensans-semibold);
@mixin lReg {
  font-family: "lato-regular", sans-serif; }
@mixin mBold {
  font-family: "montserrat-bold", sans-serif; }
@mixin mReg {
  font-family: "montserrat-regular", sans-serif; }
@mixin oEbold {
  font-family: "opensans-extrabold", sans-serif; }
@mixin oItal {
  font-family: "opensans-italic", sans-serif; }
@mixin oReg {
  font-family: "opensans-regular", sans-serif; }
@mixin oSbold {
  font-family: "opensans-semibold", sans-serif; }
