@import "helpers/all";
@import "fonts";
@import "libs/_libs";

@mixin t {
  border: 1px solid red; }
@mixin w($size, $contain) {
   width: $size / $contain * 100%; }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  @include r(600) {
    font-size: 50%; }
  @include r(350) {
    font-size: 45%; } }
*, *:before, *:after {
  box-sizing: inherit; }
body {
  min-width: 320px;
  width: 100%;
  height: 100%;
  font-size: 16px;
  background-color: #5ec2e7; }

.container {
  max-width: 98rem;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 2rem; }
.cover {
  position: relative;
  width: 100%;
  min-height: 600px;
  height: 100vh;
  background-size: cover;
  background-position: bottom center;
  &__link {
    position: absolute;
    bottom: 10%;
    left: 50%;
    transform: translate(-50%, -10%);
    width: 50px;
    height: 50px;
    border: 2px solid white;
    border-radius: 50%;
    transition: .25s linear;
    &:hover {
      background-color: #333;
      border: 2px solid #333; }
    svg {
      position: relative;
      top: 10px;
      left: 12px; } } }

.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 5rem;
  @include r(500) {
    flex-wrap: wrap;
    justify-content: center; }
  &__logo {
    @include r(500) {
      text-align: center;
      flex: 1 0 100%; }
    a {
      display: inline-block;
      text-decoration: none;
      @include mBold;
      font-size: 2.8rem;
      color: #fff; } } }
.hero {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  @include vcenter;
  &__title {
    @include mBold;
    font-size: 6rem;
    padding: 0; }
  &__subtitle {
    @include mReg;
    font-size: 1.8rem;
    padding-top: 1rem; } }

.menu {
  position: relative;
  transition: .5s linear;
  &__link {
    text-align: center;
    display: inline-block;
    position: relative;
    @include mBold;
    padding-right: 2rem;
    padding-bottom: 1rem;
    color: #fff;
    text-decoration: none;
    font-size: 1.4rem;
    text-transform: uppercase;
    cursor: pointer;
    position: relative;
    &:nth-child(2):hover ~.menu__slider {
      left: 6.5rem;
      width: 8.3rem; }
    &:nth-child(3):hover ~.menu__slider {
      left: 16.8rem;
      width: 3.8rem; }
    &:nth-child(4):hover ~.menu__slider {
      left: 22.8rem;
      width: 6rem; }
    &:nth-child(5):hover ~.menu__slider {
      left: 30.8rem;
      width: 6.8rem; } }
  &__slider {
      position: absolute;
      height: 3px;
      bottom: 0;
      left: 0px;
      width: 4.5rem;

      transition: .5s linear;
      background-color: #fff; } }
.panel {
  background-color: #f4f4f4;
  &--red {
    background-color: #ff605f; }
  &__slider {
    padding-top: 10rem;
    padding-bottom: 5rem;
    margin: 0 auto; }
  &__t-slider {
    padding: 5rem 0; } }
.slider {
  width: 100%;
  figure {
    @include w(360, 940);
    display: block;
    @include r(780) {
      width: 100%; } }
  &__item {
    width: 100%;
    display: flex!important;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap; }
  &__img {
    border-radius: 4px;
    display: block;
    width: 100%; }
  &__content {
    @include w(460, 940);
    @include r(780) {
      padding-top: 2rem;
      width: 100%;
      text-align: center; } }
  &__title {
    @include mBold;
    font-size: 2.4rem;
    color: #333;
    line-height: 3.6rem; }
  &__text {
    padding-top: 2.5rem;
    line-height: 3.1rem;
    @include oReg;
    font-size: 1.6rem; } }
.t-slider {
  text-align: center;
  @include oItal;
  &__text {
    font-size: 2.4rem;
    color: #fff; }
  &__author {
    display: block;
    padding-top: 3.5rem;
    font-size: 1.6rem;
    color: #ccc;
    &:before, &:after {
      content: '-';
      display: inline-block;
      color: #ccc;
      margin: 0 3px; } } }
.section {
  background-color: #fbfbfb;
  text-align: center;
  &--grey {
    background-color: #f5f5f5; }
  &__title {
    display: inline-block;
    text-transform: uppercase;
    padding-top: 11rem;
    @include mBold;
    font-size: 1rem;
    border-bottom: 3px solid rgba(#ccc, .5);
    font-size: 1.6rem;
    padding-bottom: .5rem;
    &--big {
      @include mReg;
      text-transform: none;
      font-size: 2.8rem; } }
  &__text {

    margin: 0 auto;
    @include oReg;
    font-size: 1.6rem;
    color: #666;
    padding-top: 4rem;
    padding-bottom: 2.5rem; }
  &__grid {
    padding-top: 6.5rem; }
  &__btn {
    margin-top: 3rem;
    margin-bottom: 8rem; }
  &__cicles {
    padding-top: 10rem;
    padding-bottom: 5rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
  &__work {
    display: flex;
    justify-content: space-between;
    padding-top: 15rem;
    padding-bottom: 12rem;
    flex-wrap: wrap;
    @include r(700) {
      padding-top: 0rem; } }
  &__right {
    @include w(480, 940);
    @include r(700) {
      width: 100%;
      padding-left: 3rem;
      padding-top: 5rem; } }
  &__left {
    text-align: left;
    margin: 0;
    @include w(340, 940);
    @include r(700) {
      width: 100%;
      text-align: center; } } }
.cicle {
  @include w(212, 940);
  @include r(900) {
    margin-top: 3rem;
    width: 50%; }
  @include r(450) {
    width: 100%; }
  &__content {
    position: relative;
    z-index: 999;
    left: 0;
    top: -50%; }
  &__percent {
    @include mBold;
    color: #666;
    font-size: 5.7rem; }
  &__text {
    @include mReg;
    font-size: 1.6rem;
    color: #999; } }
.grid {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
  &__column {
    @include w(229, 940);
    padding: .5rem;
    @include r(780) {
      width: 50%; }
    @include r(450) {
      width: 100%; }
    &--big {
      @include w(462, 940);

      @include r(780) {
        width: 100%; } }
    &--first {
      @include r(780) {
        order: 2; } } }
  &__item {
    width: 100%;
    padding: 1rem 1rem 1.5rem; }
  &__figure {
    border-radius: 3rem;
    background-color: #5ec2e7;
    position: relative;
    width: 100%;
    display: block;
    margin: 0;
    padding: 0;
    overflow: hidden;
    &:hover img {
      left: -100%; }
    &:hover figcaption {
      left: 0; }
    &--big {
      padding-bottom: 3rem; } }
  &__img {
    display: block;
    width: 100%;
    border-radius: 3rem;
    position: relative;
    bottom: -100%;
    left: 0;
    transition: .5s linear; }

  &__caption {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0%;
    left: 100%;
    transition: .5s linear;
    border-radius: 3rem;
    p {
      @include oReg;
      color: #fff;
      font-size: 1.3rem;
      text-align: center;
      @include vcenter; } }


  &__row {
    width: 100%;
    display: flex;
    justify-content: space-between;
    @include r(450) {
      flex-direction: column; } } }
.btn {
  display: inline-block;
  padding: 2rem 3rem;
  @include mBold;
  color: #55c1e7;
  text-decoration: none;
  font-size: 1.4rem;
  border: 2px solid #55c1e7;
  border-radius: 5px;
  text-transform: uppercase;
  transition: .25s linear;
  &:hover {
    background-color: #55c1e7;
    color: #fff; }
  &--red {
    background-color: #fd685b;
    border: none;
    @include mReg;
    color: #fff;
    padding:  2rem 4rem; } }

.timeline {
  list-style: none;
  position: relative;
  padding: 0;
  text-align: left;
  &:before {
    content: '';
    width: 2px;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-color: #d4d4d4; }
  &__item {
    display: flex;
    padding: 4rem 2.5rem;
    position: relative;
    align-items: center;
    &:before {
      content: '';
      display: block;
      width: 1.8rem;
      height: 1.8rem;
      background-color: #f6f6f6;
      border: 3px solid #ccc;
      border-radius: 50%;
      position: absolute;
      left: -0.8rem;
      top: 45%; } }
  &__content {
    padding-left: 2rem; }
  &__title {
    font-size: 1.8rem;
    @include mBold;
    color: #333;
    padding-bottom: 1rem; }
  &__job {
    @include mReg;
    font-size: 1.4rem;
    color: #333;
    padding-bottom: 1rem; }
  &__text {
    @include mReg;
    color: #999;
    font-size: 1.3rem; }
  &__date {
    text-align: center;
    color: #ccc;
    font-size: 1.8rem;
    @include mBold;
    display: inline-block;
    width: 50px;
    background-color: #fbfbfb;
    padding: 5px 0;
    z-index: 99;
    position: relative;
    left: -25px; } }


.social {
  @include mReg;
  font-size: 1.6rem;
  color: #333;
  opacity: .3;
  text-decoration: none;
  &:hover {
    opacity: 1; }
  &:before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    margin-right: 1rem;
    width: 3.7rem;
    height: 3.7rem;
    border-radius: 50%;
    background-size: 50%;
    background-repeat: no-repeat;
    background-position: center;
    @include r(550) {
      margin-bottom: 1rem; } }
  &--facebook {
    &:before {
      background-color: #375996;
      background-image: url(../images/i-facebook.svg); } }
  &--twitter {
    &:before {
      background-color: #39a9e0;
      background-image: url(../images/i-twitter.svg); } }
  &--linkedin {
    &:before {
      background-color: #02679a;
      background-image: url(../images/i-linkedin.svg); } }
  &--google {
    &:before {
      background-color: #dd463d;
      background-image: url(../images/i-google.svg); } }
  &--rss {
    &:before {
      background-color: #ef7a00;
      background-image: url(../images/i-rss.svg); } }
  &--ghost {
    &:before {
      background-color: transparent;
      border: 2px solid #fff; }
    &:hover:before {
      background-color: #02679a;
      border: 2px solid #02679a; } } }

.contact {
  padding-bottom: 10rem;
  &__socials {
    padding-top: 2rem;
    @include r(550) {
      display: flex; }

    &>* {
      padding-bottom: 1.5rem; } }
  &__form {
    text-align: right;
    @include w(625, 940);
    @include r(550) {
      width: 100%; }
    &>* {
      margin-bottom: 2rem; } }
  &__input {
    width: 48%;
    display: inline-block;
    position: relative;
    &:first-child {
      margin-right: 3%; }
    @include r(750) {
      width: 100%; }
    &--name {
      &:before {
        content: '';
        width: 2rem;
        height: 2rem;
        display: inline-block;
        position: absolute;
        left: 1rem;
        top: 2rem;
        z-index: 999;
        background-image: url(../images/i-user-o.svg);
        background-size: cover; } }
    &--email {
      &:before {
        content: '';
        width: 2rem;
        height: 2rem;
        display: inline-block;
        position: absolute;
        left: 1rem;
        top: 1.8rem;
        z-index: 999;
        background-image: url(../images/i-envelope-o.svg);
        background-size: cover; } } }
  &__grid {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding-top: 10rem;
    flex-wrap: wrap; }
  &__info {
    text-align: left;
    @include w(220, 940);
    display: flex;
    flex-direction: column;
    @include r(550) {
      width: 100%;
      text-align: center;
      padding-top: 2rem; } }
  &__title {
    @include mBold;
    color: #ccc;
    font-size: 1.3rem;
    text-transform: uppercase; }

  &__address {
    @include oReg;
    font-style: normal;
    font-size: 1.5rem;
    color: #333;
    padding-top: 2rem;
    padding-bottom: 8rem; } }

.input {
  border-radius: 9px;
  border: 2px solid #e7eaee;
  background-color: #ffffff;
  padding: 2rem 2rem;
  position: relative;
  display: block;
  width: 100%;
  text-transform: uppercase;
  &:placeholder {
    font-size: 1.4rem;
    color: #999; }
  &:hover {
    border: 2px solid #cad1d9; }
  &:focus {
    border: 2px solid #88d3ff; }
  &--inline {
    display: inline-block;
    padding: 2rem 4rem; }
  &--textarea {
    resize: none;
    height: 14rem; } }
.footer {
  background-color: #363842;
  &__content {
    padding: 5.5rem 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
  &__title {
    display: block;
    padding-bottom: 5rem;
    @include mBold;
    color: #fff;
    font-size: 1.6rem;
    text-transform: uppercase; }
  &__column {
    text-align: left;
    &:first-child {
      @include r(500) {
        order: 9;
        width: 100%;
        h6 {
          text-align: center;
          padding-top: 1rem;
          padding-bottom: 1rem; } } } }
  &__contact {
    @include mReg;
    display: block;
    text-decoration: none;
    font-size: 1.4rem;
    color: #545766;
    &:hover {
      color: #fff; } } }

.submenu {
  &__link {
    padding-bottom: 1.5rem;
    display: block;
    @include mReg;
    text-decoration: none;
    font-size: 1.4rem;
    color: #545766;
    text-transform: uppercase;
    transition: .25s linear;
    @include r(500) {
      text-align: center;
      display: inline-block;
      padding: 0 1rem; }
    &:hover {
      color: #fff; } } }

