}

.contact__title {
  font-family: "montserrat-bold", sans-serif;
  color: #ccc;
  font-size: 1.3rem;
  text-transform: uppercase;
}

.contact__address {
  font-family: "opensans-regular", sans-serif;
  font-style: normal;
  font-size: 1.5rem;
  color: #333;
  padding-top: 2rem;
  padding-bottom: 8rem;
}

.input {
  border-radius: 9px;
  border: 2px solid #e7eaee;
  background-color: #ffffff;
  padding: 2rem 2rem;
  position: relative;
  display: block;
  width: 100%;
  text-transform: uppercase;
}

.input:placeholder {
  font-size: 1.4rem;
  color: #999;
}

.input:hover {
  border: 2px solid #cad1d9;
}

.input:focus {
  border: 2px solid #88d3ff;
}

.input--inline {
  display: inline-block;
  padding: 2rem 4rem;
}

.input--textarea {
  resize: none;
  height: 14rem;
}

.footer {
  background-color: #363842;
}

.footer__content {
  padding: 5.5rem 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer__title {
  display: block;
  padding-bottom: 5rem;
  font-family: "montserrat-bold", sans-serif;
  color: #fff;
  font-size: 1.6rem;
  text-transform: uppercase;
}

.footer__column {
  text-align: left;
}

.footer__contact {
  font-family: "montserrat-regular", sans-serif;
  display: block;
  text-decoration: none;
  font-size: 1.4rem;
  color: #545766;
}

.footer__contact:hover {
  color: #fff;
}

.submenu__link {
  padding-bottom: 1.5rem;
  display: block;
  font-family: "montserrat-regular", sans-serif;
  text-decoration: none;
  font-size: 1.4rem;
  color: #545766;
  text-transform: uppercase;
  transition: .25s linear;
}

.submenu__link:hover {
  color: #fff;
}

@media only screen and (max-width: 900px) {
  .cicle {
    margin-top: 3rem;
    width: 50%;
  }
}

@media only screen and (max-width: 780px) {
  .slider figure {
    width: 100%;
  }

  .slider__content {
    padding-top: 2rem;
    width: 100%;
    text-align: center;
  }

  .grid__column {
    width: 50%;
  }

  .grid__column--big {
    width: 100%;
  }

  .grid__column--first {
    order: 2;
  }
}

@media only screen and (max-width: 750px) {
  .contact__input {
    width: 100%;
  }
}

@media only screen and (max-width: 700px) {
  .section__work {
    padding-top: 0rem;
  }

  .section__right {
    width: 100%;
    padding-left: 3rem;
    padding-top: 5rem;
  }

  .section__left {
    width: 100%;
    text-align: center;
  }
}

@media only screen and (max-width: 600px) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 550px) {
  .social:before {
    margin-bottom: 1rem;
  }

  .contact__socials {
    display: flex;
  }

  .contact__form {
    width: 100%;
  }

  .contact__info {
    width: 100%;
    text-align: center;
    padding-top: 2rem;
  }
}

@media only screen and (max-width: 500px) {
  .header {
    flex-wrap: wrap;
    justify-content: center;
  }

  .header__logo {
    text-align: center;
    flex: 1 0 100%;
  }

  .footer__column:first-child {
    order: 9;
    width: 100%;
  }

  .footer__column:first-child h6 {
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
  }

  .submenu__link {
    text-align: center;
    display: inline-block;
    padding: 0 1rem;
  }
}

@media only screen and (max-width: 450px) {
  .cicle {
    width: 100%;
  }

  .grid__column {
    width: 100%;
  }

  .grid__row {
    flex-direction: column;
  }
}

@media only screen and (max-width: 350px) {
  html {
    font-size: 45%;
  }
}